@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,400i,700,700i');
@import url('https://fonts.googleapis.com/css?family=Bree+Serif');
* {
box-sizing: border-box;
}

@media (min-width: 600px) {

}
p {
margin-top: 20px;
margin-bottom: 0;
padding: 0;
font-size: 20px;
line-height: 1.5;
}
.tabs {
display: flex;
justify-content: space-around;
flex-wrap: wrap;
max-width: 100%;
background: inherit;
box-shadow: 0 48px 80px -32px rgba(0,0,0,0.3);
}
.input {
position: absolute;
opacity: 0;
}
.tabLabel {
    border-radius: 15px 15px 0px 0px;
-moz-border-radius: 15px 15px 0px 0px;
-webkit-border-radius: 15px 15px 0px 0px;
margin-top: 20px;
text-align: center;
width: 90%;
padding: 20px 30px;
background: inherit;
cursor: pointer;
font-weight: bold;
font-size: 18px;
color: #7f7f7f;
background: rgba(250,250,241,255);
transition: background 0.1s, color 0.1s;
-webkit-box-shadow: inset 0px -2px 5px 1px rgba(0,0,0,0.2);
-moz-box-shadow: inset 0px -2px 5px 1px rgba(0,0,0,0.2);
box-shadow: inset 0px -2px 5px 1px rgba(0,0,0,0.2);
}

.tabLabel:active {
background: rgb(250, 227, 170);
}
.input:focus + .tabLabel {
    z-index: 1;
    }
.input:checked + .tabLabel {
-webkit-box-shadow: inset 0px 0px 0px 0px rgba(0,0,0,0);
-moz-box-shadow: inset 0px 0px 0px 0px rgba(0,0,0,0);
box-shadow: inset 0px 0px 0px 0px rgba(0,0,0,0);
background: rgb(250, 237, 205);
color: #000;
}
@media (min-width: 600px) {
.tabLabel {
width: 50%;
}
}
.panel {
display: none;
background: inherit;
}
@media (min-width: 600px) {
.panel {
background:linear-gradient(90deg, rgba(250,237,205,1) 50%, rgba(250,250,240,1) 100%);
order: 99;
}
}
.input:checked + .tabLabel + .panel {
display: block;
}
